import React, { useState, useEffect } from 'react';
import { useRecoilValue } from 'recoil';
import { useNavigate } from 'react-router-dom';

import userToken from '../../Recoil/userTokenAtom'; ////   
import { Container } from '../../Components/Common/Container';
import Gobackbtn from '../../Components/Common/GoBackbtn';
import ButtonSubmit from '../../Components/Common/Button';

import uploadFile from '../../Assets/images/upload-file.png';
import xButton from '../../Assets/icons/x.svg';
import {
  HeaderContainer,
  HiddenH1,
  UploadPostBox,
  ProfileImg,
  TextArea,
  ImgPre,
  XButton,
  InputImgIcon,
} from './UploadEditPostStyle';

export default function AddPost() {
  const token = useRecoilValue(userToken);
  const navigate = useNavigate();

  const [content, setContent] = useState('');
  const [image, setPostImage] = useState('');

  const [userImage, setUserImage] = useState('');

  const [isButtonDisabled, setIsButtonDisabled] = useState(true);

  const getUserImg = async () => {
    console.log(token);
    const res = await fetch('https://api.mandarin.weniv.co.kr/user/myinfo', {
      method: 'GET',
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    const json = await res.json();
    console.log(json);

    if (json && json.user) {
      setUserImage(json.user['image'] || '');
    }
  };

  useEffect(() => {
    //    getInitInfo  .
    getUserImg();
  }, []);

  const addPost = async (addPostData) => {
    try {
      const res = await fetch('https://api.mandarin.weniv.co.kr/post', {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-type': 'application/json',
        },
        body: JSON.stringify(addPostData),
      });
      const json = await res.json();
      console.log(json);
      navigate('/profile/myInfo');
    } catch (error) {
      alert('  !');
    }
  };

  const inputContent = (e) => {
    setContent(e.target.value);
  };

  const uploadImage = async (imageFile) => {
    const baseUrl = 'https://api.mandarin.weniv.co.kr/';
    const reqUrl = baseUrl + 'image/uploadfile';
    //  
    const form = new FormData();
    //   
    // .append("","")
    form.append('image', imageFile);
    //   
    const res = await fetch(reqUrl, {
      method: 'POST',
      body: form,
    });
    const json = await res.json();
    const imageUrl = baseUrl + json.filename;
    setPostImage(imageUrl);
  };

  const handleChangeImage = (e) => {
    //  
    const imageFile = e.target.files[0];
    uploadImage(imageFile);
  };

  //textarea  
  const adjustTextareaHeight = (event) => {
    const textarea = event.target;
    //  
    textarea.style.height = 'auto';
    //   textarea  
    textarea.style.height = `${textarea.scrollHeight}px`;
  };

  const submitAddPost = (e) => {
    e.preventDefault();

    const addPostData = {
      post: {
        content,
        image,
      },
    };
    addPost(addPostData);
  };

  const closeImg = () => {
    setPostImage('');
    document.getElementById('profileImg').value = ''; //   
  };

  useEffect(() => {
    if (content.trim() === '' && !image) {
      setIsButtonDisabled(true);
    } else {
      setIsButtonDisabled(false);
    }
  }, [content, image]);

  const handleFormSubmit = (e) => {
    e.preventDefault();
    submitAddPost(e);
  };

  return (
    <Container>
      <HeaderContainer>
        <Gobackbtn />
        <ButtonSubmit
          buttonText=''
          onClickHandler={submitAddPost}
          disabled={isButtonDisabled}
        />
      </HeaderContainer>
      <UploadPostBox>
        <section>
          <HiddenH1> </HiddenH1>

          <form onSubmit={handleFormSubmit}>
            <ProfileImg>
              {/*   */}
              <img src={userImage} alt='' />
            </ProfileImg>
            <TextArea>
              <div>
                {/*    */}
                <textarea
                  value={content}
                  onChange={(e) => {
                    inputContent(e);
                    adjustTextareaHeight(e);
                  }}
                  id='contentTextarea'
                  name='content'
                  rows='1'
                  cols='50'
                  placeholder=' '
                ></textarea>
              </div>
            </TextArea>

            <div>
              {/*   */}
              {image ? (
                <ImgPre>
                  <img src={image} alt='' id='imagePre' />
                  <XButton>
                    <button type='button' onClick={closeImg}>
                      <img src={xButton} alt='' />
                    </button>
                  </XButton>
                </ImgPre>
              ) : null}

              {/*    */}
              <InputImgIcon>
                <label htmlFor='profileImg'>
                  <img src={uploadFile} alt='' />
                </label>
                <input
                  type='file'
                  onChange={handleChangeImage}
                  id='profileImg'
                  name='image'
                  accept='image/*'
                  style={{ display: 'none' }}
                />
              </InputImgIcon>
            </div>
          </form>
        </section>
      </UploadPostBox>
    </Container>
  );
}
