import React, { useState, useEffect } from 'react';
import { useRecoilValue } from 'recoil';
import { useNavigate } from 'react-router-dom';
import userToken from '../../Recoil/userTokenAtom'; // 
import { Container } from '../../Components/Common/Container';
import Gobackbtn from '../../Components/Common/GoBackbtn';
import ButtonSubmit from '../../Components/Common/Button';
import uploadFile from '../../Assets/images/upload-file.png';
import styled from 'styled-components';

import { HeaderContainer, HiddenH1 } from '../Post/UploadEditPostStyle';

function EditProfile() {
  //   
  const token = useRecoilValue(userToken);
  const navigate = useNavigate();

  const [username, setUsername] = useState('');
  const [accountname, setAccountname] = useState('');
  const [imgSrc, setImgSrc] = useState('');
  const [intro, setIntro] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [accountError, setAccountError] = useState('');
  const [accountLengthError, setAccountLengthError] = useState('');
  const [introError, setIntroError] = useState('');
  const [duplicateIdError, setDuplicateIdError] = useState('');
  const [userNameError, setUserNameError] = useState('');
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [userType, setUserType] = useState('');

  //   API
  const getInitInfo = async () => {
    console.log(token);
    const res = await fetch('https://api.mandarin.weniv.co.kr/user/myinfo', {
      method: 'GET',
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    const json = await res.json();
    console.log(json);

    if (json && json.user) {
      setImgSrc(json.user['image'] || '');

      setAccountname(json.user['accountname']);

      setUserType(json.user['username'].slice(0, 3));

      setUsername(json.user['username'].slice(3, json.user['username'].length) || '');

      setIntro(json.user['intro'] || '');
    }
  };

  useEffect(() => {
    //    getInitInfo  .
    getInitInfo();
  }, []);
  //   API
  const edit = async (editData) => {
    // const token = localStorage.getItem('token');

    const reqUrl = 'https://api.mandarin.weniv.co.kr/user';
    const res = await fetch(reqUrl, {
      method: 'PUT',
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-type': 'application/json',
      },
      body: JSON.stringify(editData),
    });
    const json = await res.json();

    if (json && json.message) {
      setDuplicateIdError(json.message); // "   ID."
    } else {
      setDuplicateIdError('');
      navigate('/profile/myInfo');
    }
  };

  const validateAccountname = (value) => {
    const pattern = /^[a-zA-Z0-9._]+$/; // , , ., _  
    if (!pattern.test(value)) {
      setAccountError(', , (.),(_)  .');
    } else if (value.length < 2 || value.length > 15) {
      setAccountLengthError('2~15  .');
    } else {
      setAccountError('');
      setAccountLengthError('');
    }
  };

  const validateUserNameLength = (value) => {
    if (value.length < 2 || value.length > 10) {
      setUserNameError('  2~10  .');
    } else {
      setUserNameError('');
    }
  };

  const validateIntroLength = (value) => {
    if (value.length < 2 || value.length > 50) {
      setIntroError('2~50  .');
    } else {
      setIntroError('');
    }
  };

  const inputUsername = (e) => {
    setUsername(e.target.value);
    validateUserNameLength(e.target.value);
  };

  const inputAccountname = (e) => {
    setAccountname(e.target.value);
    validateAccountname(e.target.value); //   

    setDuplicateIdError('');
  };
  const inputInfo = (e) => {
    setIntro(e.target.value);
    validateIntroLength(e.target.value);
  };

  const uploadImage = async (imageFile) => {
    const baseUrl = 'https://api.mandarin.weniv.co.kr/';
    const reqUrl = baseUrl + 'image/uploadfile';
    //  
    const form = new FormData();
    //   
    // .append("","")
    form.append('image', imageFile);
    //   
    const res = await fetch(reqUrl, {
      method: 'POST',
      body: form,
    });
    const json = await res.json();
    console.log(baseUrl + json.filename);
    const imageUrl = baseUrl + json.filename;
    setImgSrc(imageUrl);
  };

  const handleChangeImage = (e) => {
    //  
    const imageFile = e.target.files[0];

    //       
    if (!imageFile) {
      setErrorMessage(' .');
      return;
    }

    //   
    setErrorMessage('');

    uploadImage(imageFile);
  };

  const submitEdit = (e) => {
    e.preventDefault();

    const fullUsername = userType + username;

    const editData = {
      user: {
        username: fullUsername,
        accountname: accountname,
        intro: intro,
        image: imgSrc,
      },
    };
    edit(editData);
  };

  useEffect(() => {
    //       
    const isAllValid =
      !userNameError &&
      !accountError &&
      !introError &&
      !accountLengthError &&
      !duplicateIdError &&
      username &&
      accountname &&
      imgSrc &&
      intro;

    //   
    setIsButtonDisabled(!isAllValid);
  }, [
    userNameError,
    accountError,
    accountLengthError,
    introError,
    duplicateIdError,
    username,
    accountname,
    imgSrc,
    intro,
  ]);

  const handleFormSubmit = (e) => {
    e.preventDefault();
    submitEdit(e);
  };

  return (
    <Container>
      <HeaderContainer>
        <Gobackbtn />
        <ButtonSubmit buttonText='' onClickHandler={submitEdit} disabled={isButtonDisabled} />
      </HeaderContainer>

      <section>
        <HiddenH1>  </HiddenH1>
        <form onSubmit={handleFormSubmit}>
          {/*   */}
          <ProfileImg>
            <label htmlFor='profileImg'>
              <img src={imgSrc} alt='Profile' id='imagePre' />
              <img src={uploadFile} alt='' />
            </label>
            <input
              type='file'
              onChange={handleChangeImage}
              id='profileImg'
              name='image'
              accept='image/*'
              style={{ display: 'none' }}
              required
            />
            <EorrorMsg style={{ color: 'red' }}>{errorMessage}</EorrorMsg>
          </ProfileImg>
          <EditProfileBox>
            <div>
              <TextLabel>
                <label htmlFor='userNameInput'> </label>
              </TextLabel>
              <TextInput>
                <input
                  value={username}
                  onChange={inputUsername}
                  type='text'
                  id='userNameInput'
                  name='username'
                  placeholder='2~10  .'
                  required
                />
              </TextInput>
              {userNameError && <EorrorMsg>{userNameError}</EorrorMsg>}
            </div>
            <div>
              <TextLabel>
                <label htmlFor='userIdInput'> ID</label>
              </TextLabel>
              <TextInput>
                <input
                  value={accountname}
                  onChange={inputAccountname}
                  type='text'
                  id='userIdInput'
                  name='accountname'
                  placeholder=', , (.),(_)  .'
                  required
                />
              </TextInput>
              {duplicateIdError && <EorrorMsg>{duplicateIdError}</EorrorMsg>}
              {accountError && <EorrorMsg>{accountError}</EorrorMsg>}
              {accountLengthError && <EorrorMsg>{accountLengthError}</EorrorMsg>}
            </div>
            <div>
              <TextLabel>
                <label htmlFor='userIntroInput'></label>
              </TextLabel>
              <TextInput>
                <input
                  value={intro}
                  onChange={inputInfo}
                  type='text'
                  id='userIntroInput'
                  name='intro'
                  placeholder='     !'
                  required
                />
              </TextInput>
              {introError && <EorrorMsg>{introError}</EorrorMsg>}
            </div>
          </EditProfileBox>
        </form>
      </section>
    </Container>
  );
}
export default EditProfile;

const ProfileImg = styled.div`
  background: linear-gradient(to bottom, #ffc700 50%, #ffc700 calc(30% + 65px), transparent 50%);
  padding-top: 65px;
  padding-left: 20px;

  label {
    cursor: pointer;
    position: relative;

    img:first-child {
      width: 105px;
      height: 105px;
      border-radius: 50%;
      border: 5px solid #fff;
      background: #fff;
    }

    img:last-child {
      width: 40px;
      height: 40px;
      position: absolute;
      bottom: 0px;
      left: 85px;
    }
  }
`;

const EditProfileBox = styled.div`
  // background: yellow;
  padding: 16px;
  display: flex;
  flex-direction: column;

  gap: 16px;
`;

const TextInput = styled.div`
  width: 100%;
  input {
    width: 100%;
    border: none;
    outline: none;
    border-bottom: 1.5px solid #dbdbdb;
    letter-spacing: -1px;
    padding: 8px 0;

    font-size: 14px;

    &:focus {
      border-bottom: 1.5px solid #ffc700;
      transition: border-color 0.3s ease-in-out;
    }
  }
`;

const TextLabel = styled.div`
  font-size: 12px;
  color: #767676;
`;

const EorrorMsg = styled.p`
  color: red;
  text-align: left;
  margin-top: 6px;
`;
